  <div>
      <a href="https://github.com/getspooky/laravelDash/wiki/Getting-Started" target="_blank" class="edit-github"><i class="fa fa-github"></i>Edit On GitHub</a>
      <h2 class="getting-h2"># Getting Started </h2>
      <p class="docs-p">
        This documentation is designed for beginners who want to get started with laravel dashboard from scratch.
        It assume that you have prior experience with <a href="https://laravel.com/" target="_blank">Laravel framework </a>.<br>
        This documentation covers getting up and running with laravel dashboard.
        After reading this guide, you will know:<br>
        <i class="fa fa-paperclip"></i> How to install and how configure Laravel Dashboard.<br>
        <i class="fa fa-paperclip"></i> The general layout of laravel dashboard.<br>
        <i class="fa fa-paperclip"></i> How to customize Routes , Views , Controllers and Events .<br>
        <i class="fa fa-paperclip"></i> How to contribute to laravel dashboard .<br>
      </p>
      <h2 class="getting-h2" id="definition"># What is Laravel Dashboard ? </h2>
      <p class="docs-p">
        Developers are lazy. No, I’m not kidding—they work hard to create systems that help them avoid more work in the future. Especially, repetitive work. And there are quite extreme examples now—we don’t need to write code anymore; it is being generated for us.
        Laravel Dashboard or LaravelDah provides a powerful user interfaces for CRUD (create, read, update, delete) operations for Laravel applications.
        It offers additonal features including Charts , Panel Management , Settings , Payment System and Super Simple WYSIWYG ... <br>
      </p>
      <h2 class="getting-h2" id="structure"># Structure </h2>
      <p class="docs-p">This directory is one of the most important directories inside which you will find some other subdirectories.<br> These are :</p>
      <table>
        <tr>
          <td>Commands</td>
          <td>Interface included with Laravel dashboard. It provides a number of helpful commands that can assist you while you build your application.</td>
        </tr>
       <tr>
        <td>Config</td>
        <td>All of the configuration files for the Laravel dashboard framework are stored.</td>
       </tr>
        <tr>
          <td>Controllers</td>
          <td>Instead of defining all request handling logic in a single routes.php file , Laravel dashboard uses Controller classes.</td>
        </tr>
        <tr>
          <td>Events</td>
          <td>Events provide a simple observer implementation which allows a user to subscribe and listen to various events triggered in the web application.</td>
        </tr>
        <tr>
          <td>Helper</td>
          <td>Laravel dashboard includes a variety of global "helper" PHP functions .you are free to use them in your own applications if you find them convenient.</td>
        </tr>
        <tr>
          <td>Listeners</td>
          <td>Listeners handle all the activities mentioned in an event that is being registered.</td>
        </tr>
        <tr>
          <td>Migrations</td>
          <td>Migrations are a type of version control for your database. They allow a team to modify the database schema and stay up to date on the current schema state.</td>
        </tr>
        <tr>
          <td>Models</td>
          <td>Models are mostly used to interact with the database using Eloquent ORM.</td>
        </tr>
        <tr>
          <td>Notifications</td>
          <td>The notification system allows you to send notifications to users over different channels.</td>
        </tr>
        <tr>
          <td>Providers</td>
          <td>Providers are the central element of the initialization process where all the relevant and required code is loaded</td>
        </tr>
        <tr>
          <td>Published</td>
          <td>This directory houses your assets such as images, JavaScript, and CSS.</td>
        </tr>
        <tr>
          <td>Resources</td>
          <td>This directory contains your views as well as your raw, un-compiled assets such as LESS, SASS, or JavaScript.</td>
        </tr>
        <tr>
          <td>Routes</td>
          <td>The routes directory contains all of the route definitions for your application.</td>
        </tr>
        <tr>
          <td>Tests</td>
          <td>Test is a level of software testing where individual units/ components of a software are tested. By default
            Laravel dashboard use <a href="https://phpunit.de/">PHPUnit</a> and <a href="https://github.com/laravel/browser-kit-testing">laravel/browser-kit-testing</a>
           </td>
        </tr>
        <tr>
          <td>Traits</td>
          <td>Trait is simply a group of methods that you want include within another class.</td>
        </tr>
      </table>
     <h2 class="getting-h2" id="installation"># Installation Via Composer</h2>
     <p class="docs-p">Before you install Laravel dashboard, you should check to make sure that your system has the proper prerequisites installed.
       These include <br>
     </p>
     <ul>
       <li><a href="https://laravel.com">Laravel Framework 5.4+</a></li>
       <li><a href="https://getcomposer.org/">Composer</a></li>
       <li><a href="https://laravel-mix.com">Laravel Mix</a></li>
       <li><a href="https://nodejs.org/en/">Node.js </a> & <a href="https://www.npmjs.com/">NPM</a></li>
     </ul>
     <p>Laravel dashboard is super easy to install. After creating your new Laravel application you can include the laravelDash package with the following command:</p>
     <pre class="shell">composer require yal/laraveldash<i class="fa fa-copy" @click="doCopy('composer require yal/laraveldash')"></i></pre>
     <h2 class="getting-h2" id="configuration"># Configuration</h2>
     <p class="docs-p">Firstly, make sure to create a new database and add your database credentials to your .env file :</p>
    <pre class="shell">APP_URL=http://localhost
DB_HOST=localhost
DB_DATABASE=homestead
DB_USERNAME=homestead
DB_PASSWORD=secret</pre>
    <p class="docs-p">
      Laravel dashboard will automatically register its service provider if you are using Laravel >=5.5. If you are using Laravel dashboard with Laravel 5.3 or 5.4, add Laravel dashboard's service provider in your application's config/app.php file:
    </p>
    <pre class="shell">
    /*
    |--------------------------------------------------------------------------
    | Autoloaded Service Providers
    |--------------------------------------------------------------------------
    |
    | The service providers listed here will be automatically loaded on the
    | request to your application. Feel free to add your own services to
    | this array to grant expanded functionality to your applications.
    |
    */

    'providers' => [

        /*
         * Laravel Framework Service Providers...
         */
        Illuminate\Auth\AuthServiceProvider::class,
        Illuminate\Broadcasting\BroadcastServiceProvider::class,
        Illuminate\Bus\BusServiceProvider::class,
        Illuminate\Cache\CacheServiceProvider::class,
        Illuminate\Foundation\Providers\ConsoleSupportServiceProvider::class,
        Illuminate\Cookie\CookieServiceProvider::class,
        Illuminate\Database\DatabaseServiceProvider::class,
        Illuminate\Encryption\EncryptionServiceProvider::class,
        Illuminate\Filesystem\FilesystemServiceProvider::class,
        Illuminate\Foundation\Providers\FoundationServiceProvider::class,
        Illuminate\Hashing\HashServiceProvider::class,
        Illuminate\Mail\MailServiceProvider::class,
        Illuminate\Notifications\NotificationServiceProvider::class,
        Illuminate\Pagination\PaginationServiceProvider::class,
        Illuminate\Pipeline\PipelineServiceProvider::class,
        Illuminate\Queue\QueueServiceProvider::class,
        Illuminate\Redis\RedisServiceProvider::class,
        Illuminate\Auth\Passwords\PasswordResetServiceProvider::class,
        Illuminate\Session\SessionServiceProvider::class,
        Illuminate\Translation\TranslationServiceProvider::class,
        Illuminate\Validation\ValidationServiceProvider::class,
        Illuminate\View\ViewServiceProvider::class,
        /*
         * Package Service Providers...
         */

        /*
         * Application Service Providers...
         */
        App\Providers\AppServiceProvider::class,
        App\Providers\AuthServiceProvider::class,
        App\Providers\BroadcastServiceProvider::class,
        App\Providers\EventServiceProvider::class,
        App\Providers\RouteServiceProvider::class,

        /*
         * Laravel dashboard Service Provider
         */
        \Yasser\LaravelDashboard\DashboardServiceProvider::class,

    ],
    </pre>
    <p class="docs-p">Next, you need to publish the laravel dashboard configuration file:</p>
    <pre class="shell">
 php artisan vendor:publish --provider="Yasser\LaravelDashboard\DashboardServiceProvider" --tag="config"</pre>
    <p class="docs-p">This is the default content of the config file that will be published as config/laravel_dashboard.php:</p>
