    <h2 id="migration" class="getting-h2"># Migration</h2>
    <p class="docs-p">Generated migrations are regular Laravel migrations , Run the migrations with:</p>
    <pre class="shell">php artisan migrate<i class="fa fa-copy" @click="doCopy('php artisan migrate')"></i></pre>
