    <h2 id="RelationShips" class="getting-h2">Defining Relationships</h2>
    <p class="docs-p">Database tables are often related to one another. For example, a blog post may have many comments, or an order could be related to the user who placed it. Eloquent makes managing and working with these relationships easy.
      so go to App\User.php and add <mark style="background-color:rgb(213, 35, 73);padding:5px;color:white;">UserRelation</mark>
    </p>
   <?php

namespace App;

use Illuminate\Notifications\Notifiable;
use Illuminate\Foundation\Auth\User as Authenticatable;
use Yasser\LaravelDashboard\Traits\UserRelation;

class User extends Authenticatable
{
    use Notifiable,UserRelation;

}
    </pre>
