    <h2 class="getting-h2" id="resource"># Defining Resources</h2>
    <p class="docs-p">By default, Laravel dashboard resources are stored in the resources directory of your application. You may generate a new resource using the dashboard:resource Artisan command:</p>
    <pre class="shell">php artisan LaravelDash:resource {name}<i class="fa fa-copy" @click="doCopy('php artisan LaravelDash:resource {name}')"></i></pre>
  <h2 class="getting-h2" id="helper"># Helper Methods</h2>
   <p class="docs-p">Laravel Dashboard has several helper functions that are ready to use. Here you can find the list of available function that may speed up your development.</p>
   <table>
     <tr>
       <td>Method</td>
       <td>Description</td>
       <td>Namespace</td>
     </tr>
     <tr>
       <td>load($folder,$file),loadImg($file)</td>
