     </tr>
   </table>
    <h2 class="getting-h2" id="artisan">Yah! You're on Laravel dashboard !</h2>
    <p class="docs-p">To see it, you need to start a web server on your development machine. You can do this by running the following command:</p>
    <pre class="shell">php artisan serve<i class="fa fa-copy" @click="doCopy('php artisan serve')"></i></pre>
    <p>To see your application in action, open a browser window and navigate to http://localhost:8000. You should see the Rails default information page:</p>
