    <h2 class="getting-h2" id="contribute"># Contribute</h2>
    <p class="docs-p">
      The people who contribute to Laravel dashboard do so for the love of open source, our users and ecosystem, and most importantly, pushing the web forward together. Because of our Open Collective model for funding and transparency, we are able to funnel support and funds through contributors, dependent projects, and the contributor and core teams. To make a donation, simply click the button below...
      <br>
    </p>
    <h2 class="getting-h2" id="help"># How Can I Help?</h2>
    <p class="docs-p">Anybody can help by doing any of the following:</p>
    <ul>
      <li><a href="">Help us write and maintain the content on this site (see the writer's guide).</a></li>
      <li><a href="">Contribute to the core repository.</a></li>
      <li><a href="">Ask your employer to use Laravel dashboard in projects.</a></li>
      <li><a href="">Make a tutorial that you explain Laravel dashboard.</a></li>
      <li><a href="">Follow our Laravel dashboad repository.</a></li>
    </ul>
    <h2 class="getting-h2" id="bug"># Bug Report</h2>
    <p class="docs-p">
      If you've found a problem in Laravel dashboard which is not a security risk, do a search on GitHub under Issues in case it has already been reported. If you are unable to find any open GitHub issues addressing the problem you found, your next step will be to open a new one. (See the next section for reporting security issues.)
      your issue should contain a title and a clear description of the issue. You should also include as much relevant information as possible and a code sample that demonstrates the issue. The goal of a bug report is to make it easy for yourself - and others - to replicate the bug and develop a fix.
      Remember, bug reports are created in the hope that others with the same problem will be able to collaborate with you on solving it. Do not expect that the bug report will automatically see any activity or that others will jump to fix it. Creating a bug report serves to help yourself and others start on the path of fixing the problem.
    </p>
