<template>
  <div>
    <Header></Header>
    <section id="main">
      <div class="container">
        <div class="row">
          <div class="col-10">
            <section>
              <!--  Team -->
                <h2>Meet the Team</h2>
                <p>
                  LaravelDash is built by a team of engineers who share a passion for making web development feel effortless. We believe that writing beautiful apps should be joyful and fun. We're building a platform for the future.
                </p>
              <div class="card" v-for="contributor in data">
                <div class="box">
                  <div class="img">
                    <img :src="contributor.avatar_url">
                  </div>
                  <h2>{{ contributor.login}}<br><strong>{{ contributor.contributions}} Commits</strong></h2>
                  <button class="btn btn-lg btn-block-github" @click="github(contributor.html_url)"><i class="fa fa-github"></i> GitHub</button>
                </div>
              </div>
              <!-- end team -->
              <!-- Sponsors
               <h2 style="margin-top:55px !important;">One-time Donations</h2>
               <p>We accept donations through these channels:</p>
               <ul class="donation">
                 <li><img src="../assets/paypal.png"></li>
                 <li><img src="../assets/bitcoin.png"></li>
               </ul>
               end Sponsors -->
            </section>
          </div>

          <div class="col-4">
            <div class="ui half page test ad hidden-ads" data-text="Half Page"></div>
          </div>

        </div>
      </div>
    </section>
  </div>
</template>

<script>
  import axios from 'axios';
  import Header from "./Header";
  export default {
    name: 'HelloWorld',
    data(){
      return{
        data:[]
      }
    },
    components: {
      'Header': Header
    },
    methods:{
      github:(url)=>{
        window.open(url);
      }
    },
    mounted() {
      // Make a request for a user with a given ID
      axios.get('https://api.github.com/repos/getspooky/laravelDash/contributors')
        .then((response)=>{
          // handle success
          this.data = [...response.data];

          console.log(this.data);

        })
        .catch(function (error) {
          // handle error
          console.log(error);
        });

    }
  }
</script>

<style scoped>

  .mb-30{
    margin-bottom:50px !important;
  }

  h2 {
    color: #4C4C4C;
    font-size: 30px;
    font-weight: 700;
    margin-top:20px;
    margin-bottom:20px;
    font-family: 'Raleway', sans-serif;
  }

  p{
    font-size:20px;
  }

  .ion-minus{
    padding:0px 10px;
  }

  .btn-block-github{
    position:relative;
    width:100%;
    margin:10px 0px 0px 0px;
    background-color:rgb(37, 33, 34);
  }

  .card {
    position:relative;
    width:300px;
    min-height:280px !important;
    margin-right:40px !important;
    top:30px;
    margin-bottom:80px;
    display:inline-block;
    background:#fff;
    box-shadow:0 20px 50px rgba(0,0,0,.1);
    border-radius:10px;
    transition:0.5s;
  }
  .card:hover {
    box-shadow:0 30px 70px rgba(0,0,0,.2);
  }
  .card .box {
    position:absolute;
    top:50%;
    left:0;
    transform:translateY(-50%);
    text-align:center;
    padding:20px;
    box-sizing:border-box;
    width:100%;
  }
  .card .box .img {
    width:120px;
    height:120px;
    margin:0 auto;
    border-radius:50%;
    overflow:hidden;
  }
  .card .box .img img {
    width:100%;
    height:100%;
  }
  .card .box h2 {
    font-size:20px;
    color:#262626;
    margin:20px auto;
  }
  .card .box h2 span {
    font-size:14px;
    background:#e91e63;
    color:#fff;
    display:inline-block;
    padding:4px 10px;
    border-radius:15px;
  }
  .card .box p {
    color:#262626;
  }
  .card .box span {
    display:inline-flex;
  }
  .card .box ul {
    margin:0;
    padding:0;
  }

  ul.donation{
    display:block;
    list-style:none;
  }

  ul.donation li{
    float:left;
  }

</style>



