<template xmlns:v-clipboard="http://www.w3.org/1999/xhtml">
  <div class="hello">
    <Header>
      <!-- Banner -->
      <section id="banner">
        <header>
          <h2 class="spacing">Build Laravel Web Applications <br> faster than ever</h2>
          <p>Easy to install ,
            Effortless to customize.</p>
          <div class="pre-command">$
            <span ref="com"></span><button @click="doCopy"><i class="fa fa-copy"></i> Copy</button>
          </div>
        </header>
      </section>
      <!-- Intro -->
      <section id="intro" class="container">
        <div class="row">
          <div class="col-4 col-12-medium">
            <section class="first">
              <i class="icon featured fa fa-diamond"></i>
              <header>
                <h2>Modern UI</h2>
              </header>
              <p>Laravel Dashboard provides a modern, responsive interface that you can be proud to put in front of your users.</p>
            </section>
          </div>
          <div class="col-4 col-12-medium">
            <section class="middle">
              <i class="icon featured alt fa-flash"></i>
              <header>
                <h2>Ridiculously fast</h2>
              </header>
              <p>Laravel Dashboard was designed to help developers take applications from concept to completion as quickly as possible.</p>
            </section>
          </div>
          <div class="col-4 col-12-medium">
            <section class="last">
              <i class="icon featured alt2 fa fa-book"></i>
              <header>
                <h2>Easy to work with</h2>
              </header>
              <p>We have provided detailed documentation as well as specific code examples to help new users get started.</p>
            </section>
          </div>
        </div>
        <footer>
          <ul class="actions">
            <li><router-link to="/Docs" class="button large">Get Started</router-link></li>
            <li><a href="https://github.com/getspooky/LaravelDash" target="_blank" class="button alt large"><i class="fa fa-github"></i> GitHub</a></li>
          </ul>
        </footer>

      </section>
    </Header>
    <section id="main">
      <div class="container">
        <div class="row">
          <div class="col-12">
            <section>
              <div class="row">
                <div class="col-4 col-6-medium col-12-small">
                  <section class="box" style="border:none;">
                    <a href="#" class="image featured img-hover-zoom"><img :src="require('../assets/dashboard.jpg')" alt="" /></a>
                    <header>
                      <h3>Dashboard Template</h3>
                    </header>
                    <p> The design don’t just look good though, they are also fully mobile responsive to accommodate users that are accessing your app, software, or website on their smartphones and tablets, no matter what size screen they have.</p>
                  </section>
                </div>

                <div class="col-4 col-6-medium col-12-small">
                  <section class="box" style="border:none;">
                    <a href="#" class="image featured img-hover-zoom"><img :src="require('../assets/settings.jpg')" alt="" /></a>
                    <header>
                      <h3>Settings Manager</h3>
                    </header>
                    <p>The Settings section allows you to update your personal information.This can be anything from your First Name , Email , Country , Password , Address or Image. </p>
                  </section>
                </div>

                <div class="col-4 col-6-medium col-12-small">
                  <section class="box img-hover-zoom" style="border:none;">
                    <a href="#" class="image featured"><img :src="require('../assets/post.jpg')" alt="" /></a>
                    <header>
                      <h3>Vue2Editor</h3>
                    </header>
                    <p>Integration of Vue2Editor , Your editor for rich text editing built with Vue.js and Quill.js , Simple setup so you can get started quickly , Offers the options you need for more complex scenarios</p>
                  </section>
                </div>
              </div>
            </section>
          </div>
        </div>

        <!-- Become Sponsor -->
        <a href="https://www.patreon.com/yasserameur" target="_blank"><img :src="require('../assets/patreon_support.png')" style="display:block;margin:40px auto 40px auto;">
        </a>
        <!--  -->
        <!-- forkme on GitHub -->
        <img :src="require('../assets/forkme_right_gray_6d6d6d.png')" style="position:fixed;top:0px;right:0px;">

      </div>
    </section>
  </div>
</template>

<script>
  import Header from "./Header";
  export default {
  name: 'HelloWorld',
    components:{
      'Header':Header
    },
  data () {
    return {
      command :'composer require yal/laraveldash',
      i:0
    }
  },
    methods:{
      auto_write:function () {
        if(this.i<this.command.length) {
          this.$refs.com.textContent += this.command.charAt(this.i);
          this.i++;
          setTimeout(this.auto_write,150);
        }
      },
      doCopy: function () {
        this.$copyText(this.command).then(function (e) {
          // Copy Successfully
        }, function (e) {
          alert('Can not copy');
        })
      }
    },
    mounted() {
      this.auto_write();
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

.img-hover-zoom {
  height:auto; /* [1.1] Set it as per your need */
  overflow: hidden; /* [1.2] Hide the overflowing of child elements */
}

/* [2] Transition property for smooth transformation of images */
.img-hover-zoom img {
  transition: transform .5s ease;

}

/* [3] Finally, transforming the image when container gets hovered */
.img-hover-zoom:hover img {
  transform: scale(1.3);
}


</style>



