<template>
    <div>
      <div class="container">
        <div class="error-404">
          <div class="img">
            <span class="oh">OH!</span>
            <span class="triangle"></span>
          </div>
          <div class="num thirddigit">4</div>
          <div class="num seconddigit">0</div>
          <div class="num firstdigit">4</div>
        </div>
        <div class="msg">Sorry! Page not found</div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "Error"
    }
</script>

<style scoped>
  .container {
    margin: 85px;
    font-family: 'PT Sans Caption', sans-serif, 'arial', 'Times New Roman';
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
    height:90vh !important;
    flex-direction: column;
  }

  .error-404 {
    float: left;
  }

  .oh, .num {
    border-radius: 50%;
    justify-content: center;
    align-items: center;
  }

  .oh {
    margin: -50px;
    width: 80px;
    height: 80px;
    background: #535353;
    color: #A2A2A2;
    font-size: 36px;
    font-style: italic;
    display: flex;
  }

  .triangle {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 0 20px 25px;
    border-color:transparent transparent #535353 transparent;
  }

  .num {
    float: left;
    width: 150px;
    height: 150px;
    display: flex;
    background-color: #07B3F9;
    color: white;
    font-size: 120px;
  }

  .msg {
    font-size: 32px;
    color: #A2A2A2;
    margin: 20px;
  }
</style>



